import React, { useRef, useState, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import { useWidowResize } from '../../constants/utilityHooks';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';
import { Row, Col, Card, Button } from 'react-bootstrap';
import { BiExpand } from "react-icons/bi";
import { RiDragMove2Line, RiDeleteBin6Line } from "react-icons/ri";
import { GiResize } from "react-icons/gi";
import { GrAdd } from "react-icons/gr";


import arrayMove from 'array-move';
import cuid from 'cuid';
import axios from 'axios';

//components
import KPI_NEW from '../../components/kpi/KPI_NEW';
import Legend from '../../components/legend/Legend';
//charts
import StackedColumnChart from '../../components/chart/StackedColumnChart';
//modal
import AddMetricsModal from './AddMetricsModal';
//common components
import Loader from '../../components/loader/Loader';
// constant
import { API_BASE_URL } from '../../constants/config';
//style
import './sortableComponent.scss';

const SortableComponent = (props) => {
  let history = useHistory();
  let [test, setTestItems] = useState([]);
  const [modalShow, setModalShow] = useState(false);
  const [content, setContent] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [smallSize, setSmallSize] = useState({});
  console.log('test', test)
  console.log('small-size: ', smallSize)
  const fetchPretty = async () => {
    setIsLoading(true)
    await axios({
      method: 'get',
      url: `${API_BASE_URL}/executive-cockpit.json`,
    }).then(result => {
      if (result.data) {
        setTestItems(result.data);
        setIsLoading(false)
      }
    }).catch(error => {
      console.log(error);
      setIsLoading(false)
    });
  };

  useEffect(() => {
    fetchPretty();
  }, []);

  if (test) {
    test = test.map((val, index) => ({
      title: val.title,
      index: index,
      id: 'sortable-' + index,
      url: val.url,
      chart: val.chart,
      overall: val.overall,
      card_id: val._id
    }));

    console.log(test)

    var found = false;
    for (var i = 0; i < test.length; i++) {
      if (test[i].label === 'add') {
        found = true;
        break;
      }
    }
    if (!found) {
      test.push({
        title: 'Add Metro',
        id: 'sortable-add',
        index: test.length,
        label: 'add',
        card_id: 'sortable-add'
      });
    }
  }

  const goTo = (url) => {
    history.push('/executive-cockpit' + url);
  }

  const addSmallSize = (id) => {
    const obj = {
      id: id,
      className: 'small-size'
    }
    setSmallSize(obj)
    console.log('clicked: ', obj)
  }

  const onSortEnd = ({ oldIndex, newIndex }) => {
    var filtered = test.filter(function (el) { return el.label != "add"; });
    setTestItems(arrayMove(filtered, oldIndex, newIndex))
  };

  const SortableItem = SortableElement(({ item }) => (

    < div className={item.card_id === smallSize.id ? 'item small-size' : 'item'}>
      <div className="inner-item">
        {item.label === 'add' ?
          <Card className="icon-center">
            <Card.Body>
              <Button className="btn-add"
                onClick={() => {
                  setModalShow(true);
                }}><GrAdd /></Button>
            </Card.Body>
          </Card>
          :
          <Card>
            <Card.Body>
              <Card.Title className={item.card_id}> {item.title}</Card.Title>
              <div className="button-container">
                <Button className={`btn-gray btn-resize `} onClick={() => addSmallSize(item.card_id)}><GiResize /></Button>
                <Button className="btn-gray"><RiDeleteBin6Line /></Button>
                <Button className="btn-gray btn-expand" onClick={() => goTo(item.url)}><BiExpand /></Button>
              </div>
              <Row>
                {/* <Col>
                  {item.overall ?
                    <KPI_NEW
                      totalValue={item.overall.total.value}
                      totalUnit={item.overall.total.unit}
                      details={item.overall.details}
                    />
                    : null}
                </Col> */}
                <Col>
                  Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.
                  {/* {item.chart ?
                    <Legend legend={item.chart.legend} />
                    : null}

                  {item.chart ?
                    <div style={{ height: "320px" }}>
                      <StackedColumnChart
                        id={item.id}
                        hiddenValueAxis={true}
                        isOneHundredPercent={false}
                        isPercentage={false}
                        largerWidth={true}
                        data={item.chart.data}
                        legend={item.chart.legend}
                        hideLabelBullet={true}
                      />
                    </div>
                    : null} */}

                </Col>
              </Row>
            </Card.Body>
          </Card>
        }
      </div >
    </div >
  ));

  const SortableList = SortableContainer(({ items }) => (
    <div className="container">
      {items.map((item, index) => (

        <SortableItem
          key={`${item.id}`}
          index={index}
          item={item}
          className={'sortable-item'}
        />
      ))}
    </div>
  ))
  // responsive dropdowns
  const cardRef = useRef(null);
  const { width } = useWidowResize(cardRef);

  if (isLoading) return <Loader loaderRef={cardRef} withContainer />;
  return (
    <div className="SortableCards" ref={cardRef}>
      <AddMetricsModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        setContent={false}
      />
      {test ?
        <SortableList
          items={test}
          onSortEnd={onSortEnd}
          axis="xy"
          helperClass="SortableHelper"
        />
        : null}
    </div>

  );
}

export default SortableComponent;
// ReactDOM.render(<SortableComponent />, document.getElementById('root'));
